.ProjectsList {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: #CF0A0A; */
    /* align-items: center; */
    width: calc(100% );
    flex-wrap: wrap;
    margin-top: 100px;
}
.ProjectsImage {
    width: 35%;
    aspect-ratio: 4/2.2;
    object-fit: cover;
    /* background-color: #CF0A0A; */
    margin: 25px;
    overflow: hidden;
    border-radius: 5px;
    /* box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px; */
}

.ProjectCard {
    width: calc(100% );
    color:#000000;
    font-weight: 100;
    text-align: left;
    /* margin: 0px 100px 0px 100px; */
    line-height: 22px;
    font-weight: 100;
    word-spacing: 1px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    border-radius: 2px;
    padding: 20px;
    height: max-content;
    border: 0.5px  rgba(238, 238, 238,0.8) solid;
   
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    /* box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px; */
}

.ProjectCard:hover{
    transition: scale 0.4s;
    scale: 1.01;
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.ProjectCardText {
    width: 65%;
    display: flex;
    flex-direction: column;
    height: max-content;
    /* align-items: center; */
    justify-content: space-around;
    margin: 30px;
}

.ProjectName {
    font-size: 30px;
    font-weight: bold;
    color: #000000;
    margin: 20px;

}

.ProjectDescription {
    margin: 20px;
    font-weight: 500;
}
.ProjectCardButtons {
    display: flex;
    /* justify-content: center; */
    flex-direction: row;
    align-items: center;
    margin: 20px;
}

.Git {
    color: #000000;
    font-weight: bold;
    font-size: 20px;
    margin-right: 50px;
    cursor: pointer;
}

.Git:hover{
    scale: 1.02;
}

.Explore {
    color: #CF0A0A;
    font-weight: bold;
    font-size: 20px;
    /* margin: 5px; */
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.Explore:hover{
    scale: 1.02;
}

.ExploreIcon {
    width: 20px;
    margin-left: 5px;
}

.ExploreIcon:hover{
    scale: 1.02;
}

@media only screen and (max-width: 950px) {
    .ProjectsList {
        display: flex;
